:root {
  /* Light Theme for Fallback */
  --textPrimary: "#000000";
  --textSecondary: "#4D4D4D";
  --textDisabled: "#8E8E93";
  --bgBase: "#F2F2F7";
  --bgElevated: "#FFFFFF";
  --separator: "#CECED9";
  --red: "#FF3B30";
  --orange: "#FF9500";
  --yellow: "#FFCC00";
  --green: "#34C759";
  --blue: "#007AFF";
  --purple: "#AF52DE";
}

body,
#root,
#App {
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: var(--textPrimary);
  background-color: var(--bgBase);
}

h1 {
  font-family: "Kameron", serif;
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 2rem;
  letter-spacing: 1px;
  margin-top: 0rem;
  margin-bottom: 0.25rem;
  padding: 0;
}

h2 {
  font-family: "Kameron", serif;
  font-weight: bold;
  font-size: 1.25rem;
  line-height: 1.5rem;
  margin-top: 0rem;
  margin-bottom: 0.25rem;
}

h3,
h4,
h5,
h6,
th {
  margin-top: 0.25rem;
  margin-bottom: 0;
  text-transform: capitalize;
  font-weight: 500;
}

h3,
h5 {
  text-transform: uppercase;
  font-weight: bold;
}

th {
  font-weight: 500;
  text-align: left;
  padding-right: 0.5rem;
  vertical-align: top;
}

p,
li,
td {
  margin: 0.5rem 0;
  color: var(--textSecondary);
  font-size: 0.9rem;
}

em {
  font-size: 0.75rem;
}

blockquote {
  font-style: italic;
  color: var(--colorPrimary);
  margin-left: 0;
  margin-right: 0;
  padding: 0 20px;
  border-left: 2px solid var(--textSecondary);
}

blockquote p {
  font-size: 1rem;
}

pre {
  border: 1px solid var(--textSecondary);
  border-radius: 4px;
  padding: 8px;
}

code {
  color: var(--textPrimary);
  font-size: 0.8rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--colorAccent);
}

a:hover {
  text-decoration: underline;
}

a::after {
  content: "\f360";
  font-family: "Font Awesome 5 Free", sans-serif;
  font-weight: bold;
  font-style: normal;
  margin: 0 0.25rem 0 0.5rem;
  text-decoration: none;
}

img {
  object-fit: cover;
  border-radius: 8px;
}
